import * as angular from 'angular';

import mapComponent from './map.component';
import viewComponent from './view.component';
import aolFeature from './feature.component';

import {
  aolLayerTile,
  aolLayerVector,
  aolLayerGroup,
  aolLayerWebgl,
} from './layers/index';
import {
  aolSourceXyz,
  aolSourceOsm,
  aolSourceVector,
  aolSourceCluster,
  aolSourceTiledebug,
} from './sources/index';
import {
  aolGeometryLinestring,
  aolGeometryPoint,
  aolGeometryPolygon,
} from './geometry.component';
import {
  aolCoordinate,
  aolCollectionCoordinates,
} from './coordinate.component';
import {
  aolStyle,
  aolStyleStroke,
  aolStyleFill,
  aolStyleCircle,
  aolStyleIcon,
  aolStyleText,
} from './styles';

import { aolOverlay } from './overlay.component';
import {
  aolInteractionDefaults,
  aolInteractionModify,
  aolInteractionSelect,
  aolInteractionDraganddrop,
} from './interactions';
import { aolInteractionDraw } from './interactions/draw.component';

var aolModule = angular.module('aol', []);
function registerComponent(component: any) {
  aolModule.component(component.name, component);
}

[
  mapComponent,
  viewComponent,
  /** */
  aolLayerTile,
  aolLayerVector,
  aolLayerWebgl,
  aolLayerGroup,
  /** */
  aolSourceXyz,
  aolSourceOsm,
  aolSourceVector,
  aolSourceCluster,
  aolSourceTiledebug,
  /** */
  aolFeature,
  /**geo */
  aolGeometryLinestring,
  aolGeometryPoint,
  aolGeometryPolygon,
  /** */
  aolCoordinate,
  aolCollectionCoordinates,
  /** */
  aolStyle,
  aolStyleStroke,
  aolStyleCircle,
  aolStyleIcon,
  aolStyleText,
  aolStyleFill,
  /**domTOMap */
  aolOverlay,
  /** */
  aolInteractionDefaults,
  aolInteractionDraw,
  aolInteractionModify,
  aolInteractionSelect,
  aolInteractionDraganddrop,
].forEach(component => {
  registerComponent(component);
});

export default aolModule;
